"use client";
import Link from "next/link";
import { useAuth } from "../auth/AuthProvider"; 
import { useRouter } from "next/navigation";
import TypewriterClient from "../TypewriterClient";
export default function AuthButtons() {
  const { user, loading, signOut } = useAuth();
  const router = useRouter();

  if (loading) return null; // evita parpadeo inicial

  if (user) {
    return (
      <button
        onClick={() => {
          signOut();
          router.push("/"); // opcional: redirigir al home
        }}
        className="bg-white text-black px-4 py-2 rounded hover:bg-gray-200 transition"
      >
        Cerrar sesi√≥n
      </button>
    );
  }

  return (
    <div className="flex gap-4">
      <Link href="/auth/login" className="bg-white text-black px-4 py-2 rounded hover:bg-gray-200 transition block text-center">
        Login
      </Link>
      <Link href="/auth/register" className="bg-white text-black px-4 py-2 rounded hover:bg-gray-200 transition block text-center">
        Register
      </Link>
    </div>
  );
}
